.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min-content, 392px));
  grid-auto-rows: auto;
  grid-gap: 32px;
}

.poolCard {
  display: flex;
  flex-direction: column;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 4px;
    background-color: var(--theme-border-color);
    border-radius: 0 4px 4px;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: calc(var(--pool-card-progress, 0) * 1%);
    height: 4px;
    background-image: linear-gradient(90deg, #4f6ae6 0%, #00d395 100%);
    border-radius: 0 4px 4px;
  }
}

.poolCardDlRow {
  display: flex;
  align-items: center;
  padding-left: 24px;
  padding-right: 24px;

  dt {
    margin-right: 8px;
  }

  dd {
    margin-left: auto;
    text-align: right;
  }
}

.poolCardFooter {
  margin-top: auto;
  padding: 0 24px 24px;
}
